@font-face {
  font-family: SILENCE;
  src: url(../../../fonts/a_RubricaXtCn-Regular.111c7d79.ttf);
}
@font-face {
  font-family: SILENCE-1;
  src: url(../../../fonts/TTSupermolotNeue-Bold.9d1ea7c4.ttf);
}
@font-face {
  font-family: SILENCE-2;
  src: url(../../../fonts/TTSupermolotNeue-Medium.4a7a3568.ttf);
} 

.kill-log-container {
  position: fixed;
  top: 2.5vh; 
  right: 2vh; 
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-end; 
}

.kill-log {
  background-color: #333;
  color: #fff;
  padding: 1vh;
  margin-bottom: 1vh; 
  border-radius: 0.4vh;
  box-shadow: 0 0 0.5vh rgba(0, 0, 0, 0.3);
  transition: opacity 0.3s ease;
  opacity: 0;
  font-family: SILENCE-1;
  font-size: 2vh;
  font-weight: 600;
}

.kill-log.visible {
  opacity: 1;
}