@font-face {
    font-family: SILENCE;
    src: url(../../../fonts/a_RubricaXtCn-Regular.111c7d79.ttf);
}
@font-face {
    font-family: SILENCE-1;
    src: url(../../../fonts/TTSupermolotNeue-Bold.9d1ea7c4.ttf);
}
@font-face {
    font-family: SILENCE-2;
    src: url(../../../fonts/TTSupermolotNeue-Medium.4a7a3568.ttf);
}

h3 {
    font-size: 3.5vh;
    font-family: SILENCE-1;
    font-weight: 600;
    margin-top: 40px;
    text-align: center;
    color: #fff;
    font-style: italic;
    font-weight: 700;
}

.vehSearchbar {
    display: flex;
    flex-wrap: nowrap;
    margin-left: 12vh;
    margin-top: 50px;
}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

.card {
    width: 300px;
    height: 400px;
    background-color: #1e1d1dea;
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.card img {
    width: 200px;
    height: 200px;
    object-fit: contain;
    margin-bottom: 20px;
}

.card button {
    padding: 10px 20px;
    background-color: #681010f2;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.card button:hover {
    background-color: #7b1919f2;
}